<template>
    <div class="biz-content" v-show="paramTableShow">
      <div class="data">
        <div class="table">
          <base-table
          :columns="columns"
          :data="sectorParam"
          rowKey="id"></base-table>
        </div>
      </div>
      <div class="option">
        <el-button @click="edit" v-show="!editFlag">编辑</el-button>
        <el-button @click="query" v-show="editFlag">保存</el-button>
        <back-button type="next" @next="recommend">智能推荐</back-button>
      </div>
    </div>
  </template>
<script>
import {mapState} from 'vuex'
const BaseTable = () => import('@/components/BaseTable')
const BackButton = () => import('@/components/BackButton')

export default {
  name: 'ParamConfig',
  components: {BaseTable, BackButton},
  data () {
    return {
      columns: [
        {prop: 'bizSector', label: '业务板块', width: 150, align: 'center'},
        {prop: 'channel', label: '数据范围-渠道', width: 130, align: 'center', isEdit: false, type: 'select', options: [{value: '01', label: '网电'}, {value: '02', label: '传统'}]},
        {prop: 'dataRange', label: '数据范围(本级或上级)', width: 180, align: 'center', isEdit: false, type: 'select', options: [{value: '01', label: '本级'}, {value: '02', label: '上级'}]},
        {prop: 'policyRange',
          label: '保单范围(回滚月份数)',
          width: 180,
          align: 'center',
          isEdit: false,
          type: 'select',
          options: [
            {value: '12', label: '12'},
            {value: '13', label: '13'},
            {value: '14', label: '14'},
            {value: '15', label: '15'},
            {value: '16', label: '16'},
            {value: '17', label: '17'},
            {value: '18', label: '18'},
            {value: '19', label: '19'},
            {value: '20', label: '20'},
            {value: '21', label: '21'},
            {value: '22', label: '22'},
            {value: '23', label: '23'},
            {value: '24', label: '24'},
            {value: '25', label: '25'},
            {value: '26', label: '26'},
            {value: '27', label: '27'},
            {value: '28', label: '28'},
            {value: '29', label: '29'},
            {value: '30', label: '30'},
            {value: '31', label: '31'},
            {value: '32', label: '32'},
            {value: '33', label: '33'},
            {value: '34', label: '34'},
            {value: '35', label: '35'},
            {value: '36', label: '36'}
          ]
        },
        {prop: 'autoChannelDown', label: '自主渠道系数下限', width: 150, align: 'center'},
        {prop: 'autoChannelUp', label: '自主渠道系数上限', width: 150, align: 'center'},
        {prop: 'autoProtectDown', label: '自主核保系数下限', width: 150, align: 'center'},
        {prop: 'autoProtectUp', label: '自主核保系数上限', width: 150, align: 'center'},
        {prop: 'forceMarketRate', label: '交强市场费用率(%)', width: 150, align: 'center'},
        {prop: 'marketRateDown', label: '市场费用率下限（%）', width: 150, align: 'center'},
        {prop: 'marketRateUp', label: '市场费用率上限（%）', width: 150, align: 'center'},
        {prop: 'expOperateRateDown', label: '预估经营成本率下限（%）', width: 170, align: 'center'},
        {prop: 'expOperateRateUp', label: '预估经营成本率上限（%）', width: 170, align: 'center'}
      ],
      editFlag: false
    }
  },
  computed: {
    ...mapState({
      tableHeader: state => state.app.tableHeader,
      sectorParam: state => state.individual.sectorParam,
      paramTableShow: state => state.individual.paramTableShow
    })
  },
  methods: {
    edit: function () {
      this.columns.forEach(column => {
        if (column.isEdit !== undefined) {
          column.isEdit = true
        }
      })
      this.editFlag = true
    },
    query () {
      this.columns.forEach(column => {
        if (column.isEdit !== undefined) {
          column.isEdit = false
        }
      })
      this.editFlag = false
    },
    recommend () {
      this.$router.push({path: '/admin/individual/declare/recommend'})
    }
  }
}
</script>
<style scoped></style>



